<template>
  
                        <h1 class="card-title">{{name}}</h1>
              </template>

<script>

    import db from './firebaseInit'
     export default {
        name: 'ViewCow',
        data() {
            return {
                // eid: null,
                name: null
                // dept: null,
    
            }
        },
        beforeRouteEnter(to, from, next) {
            db.collection('cows').where('name', '==', to.params.name).get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    next(vm => {
                            vm.name = doc.data().name
                    })
    
                })
            })
        },
        watch: {
            '$route': 'fetchData'
        },
        methods: {
            fetchData() {
                db.collection('Cows').where(
                        'name', '==', this.$route.params.name
                    ).get()
                    .then
                    (querySnapshot => {
                        querySnapshot.forEach(doc => {
                            this.name = doc.data().name
                        })

                
                    })
            }
        }
    
    }
</script>




