<template>
    <div>
         <div id="NewCow" v-for='cow in cows', v-bind-key='cow.id'>
            <h3>NewCow</h3>
            <li>{{cow.id}}</li>

    </div>
    </div>
       
</template>

<script>

import db from './firebaseInit'

export default {
    name: 'NewCow',
  data () {
      return {
          cows:[]

  }
  },
 created() {
            db.collection('cows').get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        'id': doc.id,
                        'name': doc.data().name,
                        'color':doc.data().color,
                        'knowdob':doc.data().knowdob,
                        'dow':doc.data().dow
                    }
                     this.cows.push(data)
                });
            })
    
        }

}
</script>




