<template lang="pug">
   div
     b-list-group(v-for='cow in cows', v-bind:key='cow.id')
          b-list-group-item {{cow.name}}



</template>
   
        


<script>
    import db from './firebaseInit'
    
    export default {
        name: 'Dashboard',
        data() {
            return {
                cows: []
    
            }
        },
       created() {
            db.collection('cows').get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        'id': doc.id,
                        'name': doc.data().name,
                        'color':doc.data().color,
                        'knowdob':doc.data().knowdob,
                        'dow':doc.data().dow
                    }
                     this.cows.push(data)
                });
            })
    
        }
    }
</script>



