<template >
    <div>
  <div>
    <v-card class="elevation-1 pa-3 ma-3">
            <v-flex xs11 sm5>
      <v-dialog
        persistent
        v-model="modal"
        lazy
        full-width
        width="290px"
      >
        <v-text-field
          slot="activator"
          label="Picker in dialog"
          v-model="date"
          prepend-icon="event"
          readonly
        ></v-text-field>
        <v-date-picker v-model="date" scrollable actions>
          <template slot-scope="{ save, cancel }">
            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn flat color="primary" @click="cancel">Cancel</v-btn>
              <v-btn flat color="primary" @click="save">OK</v-btn>
            </v-card-actions>
          </template>
        </v-date-picker>
      </v-dialog>
    </v-flex>

    </v-card>
  </div>
</div>
</template>

<script>
    import db from './firebaseInit'
    export default {
        name: 'DailyYield',
        data() {
            return {
                cows: []
            }
        },
        created() {
            db.collection('cows').get().then(querySnapshot => {
                querySnapshot.forEach(doc => {
                    const data = {
                        'id': doc.id,
                        'name': doc.data().name,
                        'color': doc.data().color,
                        'knowdob': doc.data().knowdob,
                        'dow': doc.data().dow
                    }
                    this.cows.push(data)
                });
            })
        }
    }
</script>



