import Vue from 'vue'
import Router from 'vue-router'
import Dashboard from '@/components/Dashboard'
import EditCow from '@/components/EditCow'
import ViewCow from '@/components/ViewCow'
import NewCow from '@/components/NewCow'
import Cows from '@/components/Cows'
import DailyYield from '@/components/DailyYield'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Dashboard',
      component: Dashboard
    },
    {
      path: '/new',
      name: 'NewCow',
      component: NewCow
    },
    {
      path: '/dailyyield',
      name: 'DailyYield',
      component: DailyYield
    },
    {
      path: '/cows',
      name: 'Cows',
      component: Cows
    },
    {
      path: '/edit:employeeid',
      name: 'EditCow',
      component: EditCow
    },
 
    {
      path: '/:name',
      name: 'ViewCow',
      component: ViewCow
    }
  ]
})



